import React from 'react';
import { ClientCompany } from '../../../../shared/typings/client.types';
import { EditIndividualInv } from './EditIndividualInv';
import { EditBusinessInv } from './EditBusinessInv';

type Props = {
	entity: ClientCompany;
	onSuccess: () => void;
};

export function EditInvitation({ entity, onSuccess }: Props) {
	const isIndividual = entity?.is_individual;

	if (isIndividual) {
		return <EditIndividualInv onSuccess={onSuccess} entity={entity} />;
	} else {
		return <EditBusinessInv onSuccess={onSuccess} entity={entity} />;
	}
}
