import React from 'react';
import { ClientCompany } from '../../../../shared/typings/client.types';
import { IndividualFormElements } from '../IndividualFormElements';
import EntityForm from '../../../../modules/entity/forms';
import { useAppState } from '../../../../shared/state';
import { useLoadAll, useNotistack } from '../../../../shared/hooks';

type Props = {
	onSuccess: () => void;
	entity: ClientCompany;
};

export function EditIndividualInv({ onSuccess, entity }: Props) {
	const entityId = entity.id;

	const { companyId } = useAppState();
	const { showNotification } = useNotistack();
	const { reloadEntityAll } = useLoadAll();

	// const { response, loading } = useAxios({
	// 	url: `/${companyId}/my/company/clients/${entityId}/employee/`,
	// 	initialLoad: true
	// });

	if (!entity.owner) {
		return <p>No Editing Is Available</p>;
	}

	const { first_name, last_name, mobile, username } = entity.owner;

	return (
		<div>
			<>
				<EntityForm.Main
					entity="clients"
					name="AllClients"
					url={`/${companyId}/my/company/clients/${entityId}/employee/${entity.owner?.id}/`}
					method="put"
					primaryKey="id"
					sendAsFormData={false}
					// normalizeData={(data: any) => data}
					// prependData
					fields={[
						{
							name: 'first_name',
							required: true,
							value: first_name,
							onSubmitKey: ''
						},
						{
							name: 'last_name',
							required: true,
							value: last_name,
							onSubmitKey: ''
						},
						{
							name: 'email',
							onSubmitKey: '',
							required: true,
							value: username
						},
						{
							name: 'mobile',
							onSubmitKey: '',
							required: true,
							value: mobile
						}
					]}
					onSuccess={(response, resetForm): any => {
						showNotification({ message: 'Successfully Edited!' });
						reloadEntityAll({
							entity: 'clients',
							name: 'AllClients',
							url: `/${companyId}/client_companies/`,
							params: {
								page: 1,
								limit: 10
							}
						});
						onSuccess();
						resetForm();
					}}
					onError={(error, resetForm): any => {
						showNotification({
							message: 'Something went wrong',
							variant: 'error'
						});
					}}
				>
					{({ isSubmitting, values, setFieldValue, resetForm }) => {
						if (process.env.NODE_ENV === 'development') {
							console.log('individual invite create values', values);
						}
						return (
							<IndividualFormElements
								setFieldValue={setFieldValue}
								values={values}
								isSubmitting={isSubmitting}
								onSuccess={onSuccess}
								resetForm={resetForm}
								isEditing
							/>
						);
					}}
				</EntityForm.Main>
			</>
		</div>
	);
}
