import React from 'react';
import { ClientCompany } from '../../../../shared/typings/client.types';
import { BusinessFormElements } from '../BusinessFormElements';
import EntityForm from '../../../../modules/entity/forms';
import { useAppState } from '../../../../shared/state';
import { useNotistack, useUpdateEntities } from '../../../../shared/hooks';
type Props = {
	onSuccess: () => void;
	entity: ClientCompany;
};
export function EditBusinessInv({ onSuccess, entity }: Props) {
	const entityId = entity?.id;

	const { companyId } = useAppState();
	const { showNotification } = useNotistack();
	const { updateEntities } = useUpdateEntities();

	if (!entity) {
		return <p>No Editing is Available</p>;
	}

	return (
		<div>
			<EntityForm.Main
				entity="clients"
				name="AllClients"
				url={`/${companyId}/my/company/clients/${entityId}/`}
				method="put"
				primaryKey="id"
				sendAsFormData={false}
				// normalizeData={(data: any) => data}
				// prependData
				fields={[
					{
						name: 'id',
						required: false,
						value: entityId,
						onSubmitKey: ''
					},
					{
						name: 'is_individual',
						required: true,
						value: false,
						onSubmitKey: ''
					},
					{
						name: 'name',
						onSubmitKey: '',
						required: true,
						value: entity.name
					},
					{
						name: 'phone',
						onSubmitKey: '',
						required: false,
						value: entity.phone
					}
				]}
				onSuccess={(response, resetForm, formValues): any => {
					showNotification({ message: 'Successfully Edited!' });

					updateEntities({
						entity: 'clients',
						entityId: entityId,
						updatingData: {
							name: formValues?.name,
							phone: formValues?.phone
						}
					});

					onSuccess();
					resetForm();
				}}
				onError={(error, resetForm): any => {
					const errorMessageText = error?.response?.data?.message || 'Something went wrong';
					showNotification({
						message: errorMessageText,
						variant: 'error'
					});
				}}
			>
				{({ isSubmitting, values, setFieldValue, resetForm }) => {
					if (process.env.NODE_ENV === 'development') {
						console.log('business invite edit values', values);
					}
					return (
						<>
							<BusinessFormElements
								setFieldValue={setFieldValue}
								isSubmitting={isSubmitting}
								onSuccess={onSuccess}
								resetForm={resetForm}
								isEditing
							/>
						</>
					);
				}}
			</EntityForm.Main>
		</div>
	);
}
